*{
  margin:0;
  padding:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


.App{
    color:white;
    background-color:black;
    padding:10px;
    height:440px;
    width:260px;
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border: 5px ridge black;
    text-align: right;
    -webkit-box-shadow: 0 10px 15px darkgray;
            box-shadow: 0 10px 15px darkgray;
    border-radius: 5px;
}

.App-exp{
  position: absolute;
  top:1%;
  left:2%;
  font-size: 14px;
  letter-spacing: 1.2px;
}

.App-screen {
  background-color: darkgray;
  height: 60px;
  border-radius: 3px;
  margin-bottom: 5px;
  position: relative;
}


